.main_body{
    background-color: #F5F5FB;
    overflow: hidden;
    height: 100vh;
    display: grid;
    grid-template-columns: 2fr 8fr 2.5fr;
}
a{
    text-decoration: none;
    cursor: default;
}
.left_panel{
    padding-top: 48px;
    background-color: white;
    text-align: center;
}
.learning{
    margin-top: 48px;
    background-color: #92BE3D;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    width: 192px;
    height: 48px;
    font-family: 'Inter', sans-serif;
    color: white;
    font-weight: 600;
}
.active{
    display: inline-block;
    font-weight: 600;
    font-size: 16px;
    color: #272829;
    height: 100%;
    border-right: 3px solid #92BE3D;
}
.active ~ li{
    border-right: 3px solid #92BE3D;
}
.li_active{
    border-right: 3px solid #92BE3D;
}
.menu{
    margin-top: 40px;
    text-align: left;
    display: grid;
    grid-template-columns: 20px 5fr;
    grid-column-gap: 25px;
    grid-row-gap: 40px;
    align-items: center;
    grid-template-rows: auto;
    margin-left: 72px;
}
a{
    color: #8DA1B5;
}
.right_panel{
    text-align: center;
    padding-top: 48px;
    background-color: white;
}
.main_screen{
    padding-top: 48px;
    margin-left: 40px;
    margin-right: 40px;
}
ul{
    margin-top: 40px;
}
.upgrade{
    margin-top: 40px;
}
.upgrade button{
    position: relative;
    top: -80px;
    background-color: #92BE3D;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    width: 144px;
    height: 48px;
    font-family: 'Inter', sans-serif;
    color: white;
    font-weight: 600;
}