header input{
    border: none;
    outline:none;
    font-family: 'Inter', sans-serif;
    font-weight: normal;
    font-size: 15px;
    width: 100%;
    margin-right: 15px;
}
::placeholder { /* Most modern browsers support this now. */
   color: #CCD6E8;
}
.upheader{
    display: flex;
    justify-content: space-between;
}
.downheader{
    display: flex;
    justify-content: space-between;
    margin-top: 47px;
}
.downheader li{
    float: left;
    margin-left: 35px;
}
header h1{
    color: #011F3B;
    font-size: 28px;
    margin: 0;
}
form{
    background-color: white;
    display: flex;
    height: 56px;
    width: 472px;
    align-items: center;
    border-radius: 10px;
}
.search_icon{
    height: 20px;
    margin-left: 23px;
    margin-right: 14px;
}
.category_inactive{
    color: #909BA7;
}